import { doc, setDoc, getDoc } from "firebase/firestore";
import { db } from "@/firebase";

export async function addIngredientToUserCollection(userId, ingredientId, ingredientData) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    const existingData = docSnap.exists() ? docSnap.data() : {};

    if (Object.prototype.hasOwnProperty.call(existingData, ingredientId)) {
      console.log(" L'ingrdient est dj prsent.");
      return { alreadyExists: true };
    }

    const updatedData = {
      ...existingData,
      [ingredientId]: ingredientData,
    };

    await setDoc(docRef, updatedData);
    console.log(" Ingrdient ajout avec succs.");
    return { alreadyExists: false };
  } catch (error) {
    console.error(" Erreur lors de l'ajout de l'ingrdient :", error);
    throw error;
  }
}

/**
 * Ajoute un lot d'ingrdients sans craser le document complet.
 * - Calcule ce qui existe dj vs ce qui est nouveau
 * - N'crit que les nouveaux (merge: true)
 * - Retourne { added, already }
 */
export async function addIngredientsBulkToUserCollection(userId, entriesMap) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  // 1) Lire l'existant
  const snap = await getDoc(docRef);
  const existing = snap.exists() ? snap.data() : {};
  const existingIds = new Set(Object.keys(existing));

  // 2) Sparer nouveaux / dj prsents
  const incomingIds = Object.keys(entriesMap || {});
  const toInsertIds = incomingIds.filter((id) => !existingIds.has(id));
  const alreadyIds  = incomingIds.filter((id) =>  existingIds.has(id));

  // 3) Si nouveaux, crire uniquement ceux-l
  if (toInsertIds.length > 0) {
    const payload = {};
    for (const id of toInsertIds) {
      payload[id] = entriesMap[id];
    }
    await setDoc(docRef, payload, { merge: true });
  }

  return {
    added: toInsertIds.length,
    already: alreadyIds.length,
  };
}

/**
 * Supprime un ingrdient de la collection d'un utilisateur.
 * @param {string} userId - L'ID de l'utilisateur.
 * @param {string} ingredientId - L'ID de l'ingrdient  supprimer.
 */
export async function deleteIngredientFromUserCollection(userId, ingredientId) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    if (!docSnap.exists()) {
      console.warn(" Aucun document trouv.");
      return;
    }

    const data = docSnap.data();
    if (!Object.prototype.hasOwnProperty.call(data, ingredientId)) {
      console.warn(" L'ingrdient n'existe pas dans la collection.");
      return;
    }

    delete data[ingredientId];

    await setDoc(docRef, data);
    console.log(" Ingrdient supprim avec succs.");
  } catch (error) {
    console.error(" Erreur lors de la suppression de l'ingrdient :", error);
    throw error;
  }
}

/**
 * Met  jour un ingrdient en le passant en favori (favoris = true).
 * @param {string} userId - L'ID de l'utilisateur.
 * @param {string} barcode - Le code barre (cl de l'ingrdient).
 */
export async function setIngredientAsFavorite(userId, barcode, isFavorite = true) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    if (!docSnap.exists()) return;

    const data = docSnap.data();
    if (!Object.prototype.hasOwnProperty.call(data, barcode)) return;

    const updatedIngredient = {
      ...data[barcode],
      favorite: isFavorite, //  mise  jour ici
    };

    await setDoc(docRef, {
      ...data,
      [barcode]: updatedIngredient,
    });

    console.log(` Favori mis  jour pour ${barcode} : ${isFavorite}`);
  } catch (error) {
    console.error(" Erreur favori :", error);
    throw error;
  }
}


/**
 * Rcupre les ingrdients d'un utilisateur.
 * @param {string} userId - L'ID de l'utilisateur.
 * @returns {Promise<Object>} Une map d'ingrdients ou un objet vide.
 */
export async function getUserIngredients(userId) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);

    if (!docSnap.exists()) {
      console.log(" Aucun ingrdient trouv.");
      return {};
    }

    const data = docSnap.data();
    console.log(" Ingrdients rcuprs :", data);
    return data;
  } catch (error) {
    console.error(" Erreur lors de la rcupration des ingrdients :", error);
    throw error;
  }
}


// Groupe 1 - Aliments non transforms ou transforms minimalement
// Groupe 2 - Ingrdients culinaires transforms
// Groupe 3 - Aliments transforms
// Groupe 4 - Produits alimentaires et boissons ultra-transforms
export function getNovaStringAndColor(novaGroup) {
  let color = 'grey';

  switch (novaGroup) {
    case 1:
      color = 'green'; break;
    case 2:
      color = 'yellow'; break;
    case 3:
      color = 'orange'; break;
    case 4:
      color = 'red'; break;
  }

  return {
    string: `nova.${novaGroup}`, // clef  passer  $t()
    color
  };
}

