export async function searchEngine(filters, area) {
    const productCollection = collection(db, `${area}`);
    let fetchedItems = []; 

    const querySnapshot = await getDocs(
        query(productCollection)
    );

    querySnapshot.forEach(doc => {
        fetchedItems.push({ id: doc.id, ...doc.data() });
    });

    if (filters.search) {
        fetchedItems = fetchedItems.filter(item => 
            item.productName && item.productName.toLowerCase().includes(filters.search.toLowerCase())
        );
    }

    if (filters.selectedCategory && filters.selectedCategory.length > 0) {
        fetchedItems = fetchedItems.filter(item => 
            item.productCategory && // Vrifie que productCategory existe
            filters.selectedCategory.every(category => 
                item.productCategory.includes(category) // Vrifie que chaque catgorie slectionne est dans productCategory
            )
        );
    }    

    if (filters.selectedSize) {
        fetchedItems = fetchedItems.filter(item => item.productSize === filters.selectedSize);
    }

    if (filters.sortingFilter) {
        if (filters.sortingFilter === "Alphabtique : A-Z") {
            fetchedItems = fetchedItems.sort((a, b) => 
                a.productName.localeCompare(b.productName)
            );
        } else if (filters.sortingFilter === "Plus rcents") {
            fetchedItems = fetchedItems.sort((a, b) => b.addDate - a.addDate);
        } else if (filters.sortingFilter === "Plus anciens") {
            fetchedItems = fetchedItems.sort((a, b) => a.addDate - b.addDate);
        }
    } else {
        fetchedItems = fetchedItems.sort((a, b) => b.addDate - a.addDate);
    }

    return fetchedItems;
}

export async function fetchItemInfos(itemId, area) {
    const docRef = doc(db, area, itemId);
    const docSnapshot = await getDoc(docRef);

    if (docSnapshot.exists()) {
        return { id: docSnapshot.id, ...docSnapshot.data() };
    } else {
        throw new Error(`Document with ID ${itemId} not found in ${area}`);
    }
}

