export async function addNewCategory(categoryName, userId, username) {
    const categoryCollection = collection(db, "Category");
    const categoryObject = {
        name: categoryName,
        userId: userId,
        creatorUsername: username,
        categoryId: uuidv4(),
        createdAt: Date.now()
    };
    const newDoc = await addDoc(categoryCollection, categoryObject);

    return newDoc;
}

export async function deleteCategory(categoryId) {
    const categoryDocRef = doc(db, "Category", categoryId);
    await deleteDoc(categoryDocRef);

    const productCollection = collection(db, "Product");
    const mobileCollection = collection(db, "Mobile");
    const productSnapshot = await getDocs(query(productCollection));
    const mobileSnapshot = await getDocs(query(mobileCollection));

    productSnapshot.forEach(async doc => {
        const productData = doc.data();
            if (productData.productCategory.includes(categoryId)) {
                const newProductCategory = productData.productCategory.filter(category => category !== categoryId);
                await updateDoc(doc.ref, { productCategory: newProductCategory });
            }
        }
    );

    mobileSnapshot.forEach(async doc => {
        const mobileData = doc.data();
            if (mobileData.productCategory.includes(categoryId)) {
                const newMobileCategory = mobileData.productCategory.filter(category => category !== categoryId);
                await updateDoc(doc.ref, { productCategory: newMobileCategory });
            }
        }
    );

    return true;
}

//LogsSummary contient une liste de logs tris selon le mois et l'anne
export async function addNewLogEntry(logMessage, userId, username) {
    const logCollection = collection(db, "LogSummaries");
    const now = new Date();
    const year = now.getFullYear();
    const month = (now.getMonth() + 1).toString().padStart(2, "0"); // Mois au format 2 chiffres
    const summaryId = `LogSummary_${month}_${year}`;

    const logObject = {
        message: logMessage,
        userId: userId,
        username: username,
        id: uuidv4(),
        createdAt: Date.now()
    };

    const summaryDocRef = doc(logCollection, summaryId);

    try {
        // Vrifiez si le document de rsum existe
        const summaryDocSnap = await getDoc(summaryDocRef);

        if (summaryDocSnap.exists()) {
            // Si le document existe, ajoutez-y le nouveau log
            await updateDoc(summaryDocRef, {
                logs: arrayUnion(logObject)
            });
        } else {
            // Sinon, crez un nouveau document avec ce log
            await setDoc(summaryDocRef, {
                logs: [logObject]
            });
        }

        console.log(`Log ajout au rsum ${summaryId}`);
    } catch (error) {
        console.error("Erreur lors de l'ajout du log au rsum:", error);
    }
}

export async function fetchLogs() {
    const logCollection = collection(db, "LogSummaries");
    let allLogs = [];

    try {
        const logSnapshot = await getDocs(query(logCollection));

        logSnapshot.forEach(doc => {
            const logData = doc.data();
            if (Array.isArray(logData.logs)) {
                allLogs = allLogs.concat(logData.logs); // Ajoute les logs de chaque rsum
            }
        });

        // Tri des logs par date de cration
        allLogs.sort((a, b) => a.createdAt - b.createdAt);

        return allLogs;
    } catch (error) {
        console.error("Erreur lors de la rcupration des logs:", error);
        return [];
    }
}

export async function fetchLogsBetweenDates(startDate, endDate) {
    const logCollection = collection(db, "LogSummaries");
    let filteredLogs = [];

    try {
        const logSnapshot = await getDocs(query(logCollection));

        logSnapshot.forEach(doc => {
            const logData = doc.data();
            if (Array.isArray(logData.logs)) {
                // Filtrer les logs dans la plage de dates spcifie
                const logsInRange = logData.logs.filter(log => 
                    log.createdAt >= startDate && log.createdAt <= endDate
                );
                filteredLogs = filteredLogs.concat(logsInRange);
            }
        });

        // Tri des logs par date de cration
        filteredLogs.sort((a, b) => a.createdAt - b.createdAt);

        return filteredLogs;
    } catch (error) {
        console.error("Erreur lors de la rcupration des logs:", error);
        return [];
    }
}

export async function deleteLogsFromSummary(logs) {
  try {
    for (let log of logs) {
      const logDate = new Date(log.createdAt); 

      const month = (logDate.getMonth() + 1).toString().padStart(2, '0'); // Mois format  2 chiffres
      const year = logDate.getFullYear().toString(); // Anne formate sur 2 chiffres (par exemple, "25" pour 2025)

      const monthYearKey = `LogSummary_${month}_${year}`;
      
      const logSummaryDocRef = doc(db, 'LogSummaries', monthYearKey);
      const logSummaryDoc = await getDoc(logSummaryDocRef);

      if (logSummaryDoc.exists()) {
        const logSummaryData = logSummaryDoc.data();

        if (logSummaryData.logs) {
          const updatedLogs = logSummaryData.logs.filter(logItem => logItem.id !== log.id);
          await updateDoc(logSummaryDocRef, {
            logs: updatedLogs,
          });
        }
      } else {
        console.error(`Le document LogSummary pour ${month}_${year} n'existe pas.`);
      }
    }
  } catch (error) {
    console.error('Erreur lors de la suppression des logs dans LogSummaries:', error);
  }
}


