export async function exportItems(area) {
    const fetchedItems = [];
    const itemCollection = collection(db, `${area}`);
    const querySnapshot = await getDocs(
        query(itemCollection)
    );

    querySnapshot.forEach(doc => {
        fetchedItems.push({ id: doc.id, ...doc.data() });
    });

    const json = JSON.stringify(fetchedItems);
    const blob = new Blob([json], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    return url;
}

export async function exportItemsToCSV(area) {
    const fetchedItems = [];
    const itemCollection = collection(db, `${area}`);
    const querySnapshot = await getDocs(
        query(itemCollection)
    );

    querySnapshot.forEach(doc => {
        fetchedItems.push({ id: doc.id, ...doc.data() });
    });

    // Convertir les items rcuprs en CSV
    const csvRows = [];
    const headers = Object.keys(fetchedItems[0]);
    csvRows.push(headers.join(',')); 

    for (const item of fetchedItems) {
        const values = headers.map(header => {
            const escaped = ('' + item[header]).replace(/"/g, '\\"');
            return `"${escaped}"`;
        });
        csvRows.push(values.join(','));
    }

    const csv = csvRows.join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    return url;
}

export async function deleteItem(itemId, area) {
    try {
        const itemDocRef = doc(db, `${area}`, itemId); 
        await deleteDoc(itemDocRef); 
        //Si le doc est un produit il faut donc parcourir tous les mobiles pour supprimer l'id du produit supprim, cet id est stock dans la map productElements qui contient l'id du produit et la quantit requise l'id tant la cl 
        if(area === 'Product'){
            const mobileCollection = collection(db, "Mobile");
            const mobileSnapshot = await getDocs(query(mobileCollection));
            mobileSnapshot.forEach(async doc => {
                const mobileData = doc.data();
                if(mobileData.productElements[itemId]){
                    delete mobileData.productElements[itemId];
                    await updateDoc(doc.ref, { productElements: mobileData.productElements });
                }
            });
        }
    } catch (error) {
        console.error("Error deleting item: ", error);
    }
}



